<template>
	<v-app>
		<!-- Sizes your main based upon application components -->
		<v-main v-resize="onResize">
			<!-- Provides the application the proper gutter -->
			<v-container
				fluid
				v-scroll="onScroll"
				class="py-0 px-0"
			>
				<Header/>
				
				<Home ref="pageHome"/>
				
				<AboutUs ref="pageAboutUs"/>
				
				<Services ref="pageServices"/>
				
				<Training ref="pageTraining"/>
				
				<News ref="pageNews"/>
				
				<Gallery ref="pageGallery"/>
				
				<ContactUs ref="pageContactUs"/>
				
				<Footer/>
				
				<v-snackbar
					v-model="snackBar"
					:color="snackBarColor"
					bottom
					timeout="9000"
					class="text-h3 font-weight-bold"
				>
					<v-icon dark small class="pr-2">{{ snackBarIcon }}</v-icon>{{ snackBarMsg }}
					<template v-slot:action="{ attrs }">
						<v-icon small v-bind="attrs" @click="snackBarClose">mdi-close-circle</v-icon>
					</template>
				</v-snackbar>
				
			</v-container>
			
		</v-main>
		
	</v-app>
</template>

<script>

import { mapGetters } from 'vuex';
import { PAGE_RESIZE, PAGE_SCROLL, PAGE_SNACKBAR_CLOSE } from '@/store/actions/page';

/*LAYOUT*/
import Header from 		'./views/publicLayout/Header.vue'
import Footer from 		'./views/publicLayout/Footer.vue'

/*PAGES*/
import Home from 		'./views/publicPage/Home.vue'
import AboutUs from 	'./views/publicPage/About.vue'
import Services from 	'./views/publicPage/Services.vue'
import Training from 	'./views/publicPage/Training.vue'
import News from 		'./views/publicPage/News.vue'
import Gallery from 	'./views/publicPage/Gallery.vue'
import ContactUs from 	'./views/publicPage/Contact.vue'

export default {
	components: {
		Header,
		Footer,
		
		Home,
		AboutUs,
		Services,
		Training,
		News,
		Gallery,
		ContactUs
	},
	name: 'ALSTERPH',
	data() {
		return {
		}
	},
	mounted () {
		this.onResize();
		
	//	this.$vuetify.theme.dark = true;
    },
	computed:{
		...mapGetters(['snackBar', 'snackBarColor', 'snackBarIcon', 'snackBarMsg'])
	},
    methods: {
		onResize () {
			this.$store.dispatch(PAGE_RESIZE);
		},
		onScroll () {
			this.$store.dispatch(PAGE_SCROLL);
		},
		snackBarClose (){
			this.$store.dispatch(PAGE_SNACKBAR_CLOSE);
		},
    }
};
</script>