<template>
	<v-container fluid class="px-0 mx-0" v-intersect="pageViewIn">
		<v-row
			no-gutters
			align="center"
			justify="center"
			class="my-12 px-md-2"
		>
			<v-col cols="12" sm="10" md="6" lg="5" xl="5" class="text-center pb-12 pb-sm-12 pb-md-0 pb-lg-0 pb-xl-0 px-0 pr-sm-5 pr-md-5 pr-lg-5 pr-xl-5">
				<v-card color="grey" height="530" width="100%" outlined class="text-h3 mt-5">
					<v-img
						:src="require('@/assets/images/googleMap.png')"
						width="100%"
						height="100%"
						class="mx-md-auto mx-lg-auto mx-xl-auto"
					>
						<div
							class="grey darken-2"
							style="height: 100%; opacity: 0.2;"
						></div>
					</v-img>
				</v-card>
			</v-col>
			
			<v-col cols="11" sm="10" md="6" lg="5" xl="5">
				<v-form
					v-view="viewContactForm"
					v-model="contactUsValid"
					ref="contactUsForm"
					lazy-validation
				>
					<v-col class="text-h5">
						MAIL US YOUR MESSAGE
					</v-col>
					
					<v-col class="pb-0">
						<v-text-field
							v-model="name"
							label="Name"
							outlined
							dense
							tile
							required
							validate-on-blur
							:rules="nameRules"
						></v-text-field>
					</v-col>
					
					<v-col class="pb-0">
						<v-text-field
							v-model="email"
							label="Email"
							outlined
							dense
							tile
							required
							validate-on-blur
							:rules="emailRules"
						></v-text-field>
					</v-col>
					
					<v-col class="pb-0">
						<v-select
							:items="subjectItems"
							v-model="subject"
							label="Subject"
							outlined
							dense
							tile
							required
							:rules="[v => !!v || 'Subject is required']"
						></v-select>
					</v-col>
					
					<v-col class="pb-0">
						<v-textarea
							clearable
							v-model="message"
							label="Message"
							outlined
							dense
							tile
							required
							validate-on-blur
							:rules="messageRules"
						></v-textarea>
					</v-col>
					
					<v-col class="pt-0 text-caption font-weight-light">
						Note: All fields are required
					</v-col>
					
					<v-col>
						<v-btn
							:disabled="!contactUsValid"
							:loading="sendLoading"
							outlined
							large
							color="primary"
							class="mr-4"
							@click="sendMessage"
						>
							SEND MESSAGE
						</v-btn>
					</v-col>
				</v-form>
			</v-col>
		</v-row>
		
		<v-snackbar
			v-model="contactUsSnackbar"
			color="success"
			bottom
			class="text-h3 font-weight-bold"
		>
			<v-icon dark small class="pr-2">mdi-check-circle</v-icon>MESSAGE SENT
			<template v-slot:action="{ attrs }">
				<v-icon small v-bind="attrs" @click="contactUsSnackbar = false">mdi-close-circle</v-icon>
			</template>
		</v-snackbar>
	</v-container>
</template>

<script>

import { PAGE_VIEW } from '@/store/actions/page';

export default {
	data: () => ({
		page: 'Contact Us',
		contactUsValid: true,
		contactUsSnackbar: false,
		name: '',
		email: '',
		subject: '',
		subjectItems: [
			'Apply for Cadet', 'Other Matters' 
		],
		message: '',
		sendLoading: false,
		nameRules: [
			v => !!v || 'Name is required',
			v => (v && v.length >= 2) || 'Name must be atleast 2 characters',
		],
		emailRules: [
			v => !!v || 'E-mail is required',
			v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
		],
		messageRules: [
			v => !!v || 'Message is required',
			v => (v && v.length >= 20) || 'Message must be atleast 20 characters',
		],
	}),
	methods: {
		pageViewIn: function(e){
			if(e[0].isIntersecting){
				let { page } = this;
				this.$store.dispatch(PAGE_VIEW, { page });
			}
		},
		reset () {
			this.$refs.contactUsForm.reset();
		},
		sendMessage() {
			if( this.$refs.contactUsForm.validate() ){
				this.sendLoading = true;
				
				setTimeout( () => { this.contactUsSnackbar = true; this.sendLoading = false; this.reset(); this.contactUsValid = true; }, 1000);
			}
		},
		viewContactForm () {
		
		//	console.log(e.target);
		
		}
	},
	mounted() {
		this.reset();
	}
}
</script>