<template>
	<v-container fluid class="px-0 mx-0">
		<v-card
			elevation="0"
			v-intersect="pageViewIn"
			:min-height="this.$store.getters.pageHeight"
			class="ma-0 pa-0"
		>
		
			<v-row
				align="center"
				justify="center"
				no-gutters
				class="mt-16"
			>
				<v-col
					cols="12"
					class="text-h5 text-sm-h4 text-md-h3 text-lg-h2 text-xl-h2 text-center text-uppercase font-weight-regular"
				>
					{{ page }}
				</v-col>
				
				<v-col
					cols="11"
					sm="11"
					md="10"
					lg="9"
					xl="8"
					class="mt-8 mb-16 text-center font-weight-light text-subtitle-2 text-sm-subtitle-2 text-md-subtitle-1 text-lg-subtitle-1 text-xl-h6"
				>
					{{ aboutUsText }}
				</v-col>
			</v-row>
			
			<v-row
				no-gutters
				v-view="eventPortPolio"
				align="center"
				justify="center"
				class="light-blue darken-4 py-16"
			>
				<v-col class="mb-5 text-h5 text-sm-h5 text-md-h4 text-lg-h3 text-xl-h3 text-center text-uppercase white--text font-weight-regular" cols="12">
					STRENGTHEN IN NUMBERS
				</v-col>
				
				<v-col cols="auto" align-self="start" class="text-body-1 text-sm-h6 text-md-h5 text-lg-h4 text-xl-h4 text-center serviceTitle2">
					<v-progress-circular
						:rotate="-90"
						:size="this.$store.getters.portPolioSize"
						:width="this.$store.getters.portPolioWidth"
						color="red lighten-1"
						:value="value"
						class="mt-16"
					>
						<span class="white--text">{{ numberFormat(crewServed) }}+<br/>Crew<br/>Served</span>
					</v-progress-circular>
				
					<v-progress-circular
						:rotate="-35"
						:size="this.$store.getters.portPolioSize"
						:width="this.$store.getters.portPolioWidth"
						color="yellow darken-1"
						:value="value"
						class="mb-16 mx-n7 mx-sm-n6 mx-md-n6 mx-lg-n5 mx-xl-n5"
					>
						<span class="white--text">{{ yearsInService }}<br/>Years<br/>Service</span>
					</v-progress-circular>
				
					<v-progress-circular
						:rotate="360"
						:size="this.$store.getters.portPolioSize"
						:width="this.$store.getters.portPolioWidth"
						color="light-green"
						:value="value"
						class="mt-16"
					>
						<span class="white--text">{{ vessels }}+<br/>Vessels</span>
					</v-progress-circular>
				</v-col>
			</v-row>
			
		</v-card>
	</v-container>
</template>

<script>

import { mapGetters } from 'vuex';

import { PAGE_VIEW } from '@/store/actions/page';

export default {
	name: 'App',
	data: () => ({
		value: 0,
		interval: {},
		yearsInService: 0,
		vessels:0,
		crewServed:0,
		page: 'About Us',
		aboutUsText: 'Alster International Shipping Services, Inc. (AISSI) is in existence as a duly registered company with the Securities and Exchange Commission (SEC) since 25 May 1988 and was officially issued a License to Operate as a Manning Agency by the Philippine Overseas Employment Administration (POEA) on 28 August 1988. With its profound experience strongly for twenty years, the results of hardships and dedication of all Alster personnel are evident. The company is ready to face the challenges over the following years embedding ourselves sincerely on the performance of our responsibilities and focus on our quality processes.',
	}),
	computed:{
		...mapGetters(['pageView'])
	},
	methods: {
		pageViewIn: function(e){
			if(e[0].isIntersecting){
				let { page } = this;
				this.$store.dispatch(PAGE_VIEW, { page });
			}
		},
		eventPortPolio: function(e){
			if( this.value == 0 && e.target.classes['view-in--full'] ){
				
				this.interval = setInterval(() => {
					
					this.value += 5;
					this.yearsInService = Math.round(((new Date().getFullYear() - 1988)/100)*this.value);
					this.vessels = Math.round((90/100)*this.value);
					this.crewServed = Math.round((10000/100)*this.value);
					
					if (this.value === 100)clearInterval(this.interval);
				}, 10);
			}
		},
		numberFormat: function(num){
			var num_parts = num.toString().split(".");
			num_parts[0] = num_parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
			return num_parts.join(".");
		}
	}
	
};
</script>

<style scoped>
	
	>>> .serviceTitle2 {
		text-transform: uppercase;
		letter-spacing:1px;
		font-weight:500;
		font-size:1.30rem;
    }
	
	>>> .typewriter span {
		overflow: hidden; /* Ensures the content is not revealed until the animation */
		border-right: .15em solid white; /* The typwriter cursor */
		white-space: nowrap; /* Keeps the content on a single line */
		margin: 0 auto; /* Gives that scrolling effect as the typing happens */
		letter-spacing: .15em; /* Adjust as needed */
		animation: 
			typing 3.5s steps(40, end),
			blink-caret .75s step-end infinite;
	}
	>>> .slide-y {
		-moz-transition-duration: 3s;
		-webkit-transition-duration: 3s;
		-o-transition-duration: 3s;
		transition-duration: 3s;
	}
	
</style>