import Vue from 'vue'
import VueRouter from 'vue-router'
//import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
	{ path: '/',		name: '',			meta: { title: '' } },
	{ path: '/about',	name: 'About Us',	meta: { title: ' | About Us' } },
	{ path: '/services',name: 'Services',	meta: { title: ' | Services' } },
	{ path: '/training',name: 'Training',	meta: { title: ' | Training' } },
	{ path: '/news',	name: 'News',		meta: { title: ' | News' } },
	{ path: '/gallery',	name: 'Gallery',	meta: { title: ' | Gallery' } },
	{ path: '/contact',	name: 'Contact Us',	meta: { title: ' | Contact Us' } },
	
/*  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ /*'../views/About.vue')
  }*/
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

const DEFAULT_TITLE = 'Alsterph';

router.afterEach((to) => {
    document.title = DEFAULT_TITLE+to.meta.title || DEFAULT_TITLE;
});

export default router
