<template>
	
		<v-app-bar
			:color="this.$store.getters.appBarColor"
			:dark="this.$store.getters.appBarDark"
			elevation="0"
			fixed
			dense
			v-bind:class="{ opacity: $store.getters.appBarOpacity }"
			:app="appBar"
			ref="refAppBar"
		>
			<v-row no-gutters class="hidden-md-and-up">
				<v-col cols="5">
					<v-img
						:src="require('@/assets/images/logo/alsterlogowhiteicon.png')"
						contain
						:height="logoHeight"
						:width="logoWidth"
						class="mt-1"
						alt="logo"
					>
					</v-img>
				</v-col>
				<v-col cols="2" class="text-center">
					<v-icon dark ref="tess" large @click="drawer = !drawer">mdi-menu</v-icon>
				</v-col>
				<v-col cols="5" class="text-right">
					<v-btn text class="white--text text-overline rounded-pill" style="line-height:100%;">JOIN<br/>ALSTER</v-btn>
				</v-col>
			</v-row>
			
			<v-img
				:src="require('@/assets/images/logo/alsterlogowhiteicon.png')"
				contain
				:height="logoHeight"
				:width="logoWidth"
				class="mt-1 hidden-sm-and-down"
				alt="logo"
			>
			</v-img>
			
			<transition name="slide">
				<v-tabs
					v-model="tab"
					slider-color="white"
					centered
					dark
					v-if="tabShow"
					class="hidden-sm-and-down text-center mx-auto"
					background-color="transparent"
				>
					<v-tab
						v-for="menu in tabMenu"
						:to="menu.route"
						:key="menu.route"
						color="transparent"
						@click="tabClick(menu.title)"
						class="text-uppercase font-weight-bold text-md-caption text-lg-body-1 text-xl-body-1"
					>
						{{ menu.title }}
					</v-tab>
				</v-tabs>
			</transition>
			
			<v-btn text class="white--text text-overline hidden-sm-and-down rounded-pill" style="line-height:100%;">JOIN<br/>ALSTER</v-btn>
			
			<div @click="drawer = !drawer" id="overlay" v-show="drawer" transition="slide-y-transition" class="hidden-md-and-up">
				<v-list
					dense
					nav
					tile
					color="primary"
					class="text-center pa-0"
				>
					<v-list-item
						class="ma-0"
						v-for="menu in tabMenu"
						:key="menu.route"
						:to="menu.route"
						@click="tabClick(menu.title)"
						link
						dark
					>
						<v-list-item-title class="text-uppercase">{{ menu.title }}</v-list-item-title>
					</v-list-item>
				</v-list>
			</div>
			
		</v-app-bar>
		
</template>

<script>

import { mapGetters } from 'vuex';

var scrollTimeout = '';
var scrollTimeoutFix = '';

export default {
	name: 'ALSTERPH',
	data() {
		return {
			pageDetect: false,
			overlay: false,
			tabShow: true,
			appBar: false,
			drawer: false,
			tab: null,
			tabMenu: [
				{ title: 'Home',		route : '/' },
				{ title: 'About Us',	route : '/about' },
				{ title: 'Services',	route : '/services' },
				{ title: 'Training',	route : '/training' },
				{ title: 'News',		route : '/news' },
				{ title: 'Gallery',		route : '/gallery' },
				{ title: 'Contact Us',	route : '/contact' }
			],
			appBarMode: [
				false, false, false, false, true, true, true
			],
		}
	},
	mounted () {
	
		if( this.$route.name != ''){
			setTimeout(() => { this.tabClick(this.$route.name); }, 10);
		}
		
		setTimeout(() => { this.tabShow = true; this.pageDetect = true; }, 1500);
		
    },
	computed:{
		...mapGetters(['logoWidth', 'logoHeight', 'pageView', 'pageWidth'])
	},
	watch: {
		pageView(newValue) {
			if(this.pageDetect && typeof newValue !== 'undefined'){
				
				const path = this.tabMenu[this.tabMenu.map(function(e) { return e.title; }).indexOf(newValue)].route;
				this.$router.push(path).catch(()=>{});
				this.tabClick(newValue);
				
			}
		},
		pageWidth(newValue, oldValue){
			
		//	this.tabClick(this.pageView);
			
			if(oldValue > 960 && newValue < 960 ){
				this.drawer = false;
				this.$refs.tess.$el.blur();
			}
		}
	},
	methods: {
		tabClick (index) {
			
			clearTimeout(scrollTimeout);
			clearTimeout(scrollTimeoutFix);
			
			this.pageDetect = false;
			
			var flag = true;
			
			switch (index) {
				case 'About Us':
					this.$root.$children[0].$refs.pageAboutUs.$el.scrollIntoView();
				break;
				case 'Services':
					this.$root.$children[0].$refs.pageServices.$el.scrollIntoView();
				break;
				case 'Training':
					this.$root.$children[0].$refs.pageTraining.$el.scrollIntoView();
				break;
				case 'News':
					this.$root.$children[0].$refs.pageNews.$el.scrollIntoView();
				break;
				case 'Gallery':
					this.$root.$children[0].$refs.pageGallery.$el.scrollIntoView();
				break;
				case 'Contact Us':
					this.$root.$children[0].$refs.pageContactUs.$el.scrollIntoView();
				break;
				default: //case 'Home':
					this.$root.$children[0].$refs.pageHome.$el.scrollIntoView();
					flag = false;
				break;
			}
			
			if(flag){
				scrollTimeoutFix = setTimeout(() => { window.scrollTo(0, window.scrollY + 10); }, 500);
			}
			scrollTimeout = setTimeout(() => { this.pageDetect = true; }, 900);
		},
	},
}

</script>

<style scoped>
	>>> #overlay {
		position: fixed; /* Sit on top of the page content */
		width: 100%; /* Full width (cover the whole page) */
		height: 100000px; /* Full height (cover the whole page) */
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(0,0,0,0.5); /* Black background with opacity */
		z-index: 5; /* Specify a stack order in case you're using a different order for other elements */
		cursor: pointer; /* Add a pointer on hover */
	}
	
	>>> .slide-enter-active {
		-moz-transition-duration: 1.5s;
		-webkit-transition-duration: 1.5s;
		-o-transition-duration: 1.5s;
		transition-duration: 1.5s;
		-moz-transition-timing-function: ease-out;
		-webkit-transition-timing-function: ease-out;
		-o-transition-timing-function: ease-out;
		transition-timing-function: ease-out;
	}

	>>> .slide-enter-to{
		opacity: 1;
	}
	
	>>> .slide-enter{
		opacity: 0;
	}
</style>