import { PAGE_RESIZE, PAGE_SCROLL, PAGE_SNACKBAR_CLOSE, PAGE_NOTIFICATION, PAGE_VIEW } from '../actions/page'
import vuetify from './../../plugins/vuetify';

const state = { 
	pageHeight		: window.innerHeight, 
	pageWidth		: window.innerWidth,
	appBarColor		: 'transparent',
	appBarDark		: false,
	appBarOpacity	: false,
	
	logoWidth		: 160,
	logoHeight		: 40,
	
	pageView		: '',
	
	portPolioSize	: 220,
	portPolioWidth	: 10,
	
//	galleryImageW	: 150,
	galleryImageH	: 100,
	
	newsMainImageW	: 100,
	newsMainImageH	: 100,
	
	newsListImageW	: 100,
	newsListImageH	: 100,
	
	trainingWindowH	: 100,
	trainingWindowW	: 100,
	trainingImageW	: 100,
	
	snackBar		: false,
	snackBarColor	: 'success',
	snackBarIcon	: 'mdi-check',
	snackBarMsg		: '.',
	
	galleryImages	: 8,
}

const getters = {
	pageHeight		: state => state.pageHeight,
	pageWidth		: state => state.pageWidth,
	appBarColor		: state => state.appBarColor,
	appBarDark		: state => state.appBarDark,
	appBarOpacity	: state => state.appBarOpacity,
	
	logoWidth		: state => state.logoWidth,
	logoHeight		: state => state.logoHeight,
	
	pageView		: state => state.pageView,
	
	portPolioSize	: state => state.portPolioSize,
	portPolioWidth	: state => state.portPolioWidth,
	
//	galleryImageW	:  state => state.galleryImageW,
	galleryImageH	: state => state.galleryImageH,
	
	newsMainImageW	: state => state.newsMainImageW,
	newsMainImageH	: state => state.newsMainImageH,
	
	newsListImageW	: state => state.newsListImageW,
	newsListImageH	: state => state.newsListImageH,
	
	trainingWindowH	: state => state.trainingWindowH,
	trainingWindowW	: state => state.trainingWindowW,
	trainingImageW	: state => state.trainingImageW,
	
	snackBar		: state => state.snackBar,
	snackBarColor	: state => state.snackBarColor,
	snackBarIcon	: state => state.snackBarIcon,
	snackBarMsg		: state => state.snackBarMsg,
	
	galleryImages	: state => state.galleryImages,
	
}

const actions = {
	[PAGE_RESIZE]: ({commit}) => {
		commit(PAGE_RESIZE)
	},
	[PAGE_SCROLL]: ({commit}) => {
		commit(PAGE_SCROLL)
	},
	[PAGE_SNACKBAR_CLOSE]: ({commit}) => {
		commit(PAGE_SNACKBAR_CLOSE)
	},
	[PAGE_NOTIFICATION]: ({commit}) => {
		commit(PAGE_NOTIFICATION)
	},
	[PAGE_VIEW]: ({commit}, data) => {
		commit(PAGE_VIEW, data.page)
	},
	
}

const mutations = {
	[PAGE_VIEW]: (state, page) => {
		state.pageView = page;
	},
	[PAGE_RESIZE]: (state) => {
		state.pageHeight = ( window.innerHeight > 300 )? window.innerHeight : 300;
		state.pageWidth = ( window.innerWidth > 300 )? window.innerWidth : 300;
		
	//	state.galleryImageW = ( vuetify.framework.breakpoint.name == 'xs' )? window.innerWidth/2 : window.innerWidth/4;
	//	state.galleryImageH = ( vuetify.framework.breakpoint.name == 'xs' )? window.innerWidth/2 - (window.innerWidth/5) : (window.innerWidth/4) - (window.innerWidth/9);
		
		
		
		console.log(vuetify.framework.breakpoint.name);
		
		var breakpointName = '';
		
		//recreate vuetify breakpoint to fix delay
		if(window.innerWidth <= 600){
			breakpointName = 'xs';
		}else if(window.innerWidth > 600 && window.innerWidth <= 960){
			breakpointName = 'sm';
		}else if(window.innerWidth > 960 && window.innerWidth <= 1264){
			breakpointName = 'md';
		}else if(window.innerWidth > 1264 && window.innerWidth <= 1904){
			breakpointName = 'lg';
		}else{
			breakpointName = 'xl';
		}
		
		switch (breakpointName) {
			case 'xs':
			
				state.logoWidth = 115;
				state.logoHeight = 35;
	
				state.portPolioSize	= 100;
				state.portPolioWidth= 5;
				
				state.trainingImageW = ((window.innerWidth/12) * 11) / 1.5;
				state.trainingWindowW = (window.innerWidth/12) * 10;
				state.trainingWindowH = (window.innerHeight/100) * 70;
		
				state.newsMainImageW = window.innerWidth;
				state.newsMainImageH = (window.innerWidth/100) * 50;
		
				state.newsListImageW = (window.innerWidth/2);
				state.newsListImageH = ((window.innerWidth/2)/100) * 60;
				
				state.galleryImageH = ((window.innerWidth/2)/100) * 60;
				state.galleryImages = 6;
			break;
			case 'sm':
			
				state.logoWidth = 140;
				state.logoHeight = 35;
	
				state.portPolioSize	= 130;
				state.portPolioWidth= 6;
				
				state.trainingImageW = ((window.innerWidth/12) * 11) / 3.2;
				state.trainingWindowW = (window.innerWidth/12) * 10;
				state.trainingWindowH = (window.innerHeight/100) * 70;
		
				state.newsMainImageW = (window.innerWidth/12) * 10;
				state.newsMainImageH = (state.newsMainImageW/100) * 50;
		
				state.newsListImageW = (window.innerWidth/12) * 5;
				state.newsListImageH = (state.newsListImageW/100) * 60;
				
				state.galleryImageH = ((window.innerWidth/2)/100) * 60;
				state.galleryImages = 6;
			break;
			case 'md':
				
				state.logoWidth = 140;
				state.logoHeight = 35;
	
				state.portPolioSize	= 150;
				state.portPolioWidth= 7;
				
				state.trainingImageW = ((window.innerWidth/12) * 11) / 3.2;
				state.trainingWindowW = (window.innerWidth/12) * 10;
				state.trainingWindowH = (window.innerHeight/100) * 70;
		
				state.newsMainImageW = (window.innerWidth/12) * 10;
				state.newsMainImageH = (state.newsMainImageW/100) * 40;
		
				state.newsListImageW = (window.innerWidth/12) * 5;
				state.newsListImageH = (state.newsListImageW/100) * 50;
				
				state.galleryImageH = ((window.innerWidth/3)/100) * 60;
				state.galleryImages = 6;
			break;
			case 'lg':
				
				state.logoWidth = 160;
				state.logoHeight = 40;
	
				state.portPolioSize	= 200;
				state.portPolioWidth= 8;
				
				state.trainingImageW = ((window.innerWidth/12) * 11) / 4;
				state.trainingWindowW = (window.innerWidth/12) * 10;
				state.trainingWindowH = (window.innerHeight/100) * 70;
		
				state.newsMainImageW = (window.innerWidth/12) * 5;
				state.newsMainImageH = (state.newsMainImageW/100) * 60;
		
				state.newsListImageW = state.newsMainImageW/2;
				state.newsListImageH = (state.newsMainImageH/2) - 4;
				
				state.galleryImageH = ((window.innerWidth/3)/100) * 60;
				state.galleryImages = 6;
			break;
			case 'xl':
				
				state.logoWidth = 160;
				state.logoHeight = 40;
	
				state.portPolioSize	= 200;
				state.portPolioWidth= 8;
				
				state.trainingImageW = ((window.innerWidth/12) * 11) / 4;
				state.trainingWindowW = (window.innerWidth/12) * 10;
				state.trainingWindowH = (window.innerHeight/100) * 70;
		
				state.newsMainImageW = (window.innerWidth/12) * 5;
				state.newsMainImageH = (state.newsMainImageW/100) * 60;
		
				state.newsListImageW = state.newsMainImageW/2;
				state.newsListImageH = (state.newsMainImageH/2) - 4;
				
				state.galleryImageH = ((window.innerWidth/4)/100) * 60;
				state.galleryImages = 8;
			break;
        }
	
	},
	[PAGE_SCROLL]: (state) => {
		
		const height = state.pageHeight/5;
		
		if( (height * 1) >= window.scrollY ){
			state.appBarColor = "transparent";
			state.appBarDark = false;
			state.appBarOpacity = false;
		}else if( (height * 2) >= window.scrollY ){
			state.appBarColor = "blue darken-1";
			state.appBarDark = false;
			state.appBarOpacity = true;
		}else if( (height * 3) >= window.scrollY ){
			state.appBarColor = "blue darken-2";
			state.appBarDark = true;
			state.appBarOpacity = true;
		}else if( (height * 4) >= window.scrollY ){
			state.appBarColor = "blue darken-3";
			state.appBarDark = true;
			state.appBarOpacity = true;
		}else{
			state.appBarColor = "blue darken-4";
			state.appBarDark = true;
			state.appBarOpacity = false;
		}
	},
	[PAGE_SNACKBAR_CLOSE]: (state) => {
		state.snackBar = false;
	},
	[PAGE_NOTIFICATION]: (state) => {
		state.snackBar		= true;
		state.snackBarColor	= 'success';
		state.snackBarIcon	= 'mdi-check';
		state.snackBarMsg	= 'Thank you for subscribing';
		
		setTimeout( () => { state.snackBar = false; }, 6000);
	},
}

export default {
	state,
	getters,
	actions,
	mutations,
}