<template>

	<v-footer
		dark
		padless
	>
		<v-container fluid class="pa-0">
					
			<v-row no-gutters class="primary px-5" align="end" justify="end">
				<v-col cols="12" sm="6" md="4" lg="3" xl="3" class="text-h6 font-weight-bold">
					<span class="pr-2">GET SOCIAL</span>
					
					<v-btn icon
						color="white"
						@click="gotoFacebook"
						class="my-2"
					>
						<v-icon large>mdi-facebook</v-icon>
					</v-btn>
				</v-col>
				
				<v-col cols="12" sm="6" class="text-h6 font-weight-bold hidden-xs-only hidden-md-and-up">
					<v-row no-gutters class="pb-2">
						<v-col cols="auto" class="pr-2">CERTIFICATIONS</v-col>
						
						<v-col cols="auto">
						<v-img
							:src="require('@/assets/images/logo/isoLogo.png')"
							width="35"
							height="35"
						></v-img>
						</v-col>
					</v-row>
				</v-col>
			</v-row>
			
			<v-row no-gutters class="px-5 mt-2">
				<v-col cols="12" sm="6" md="4" lg="3" xl="3">
					<span class="font-weight-bold text-h6">
						GET IN TOUCH<br/>
					</span>	
					<span class="caption font-weight-light">
						<v-icon small>mdi-map-marker</v-icon> 2/F 2053 Bldg. Edison St. Makati City <span class="hidden-sm-and-down">Philippines</span><span class="hidden-md-and-up">Phils.</span><br/>
						<v-icon small>mdi-phone</v-icon> +63 2 8887 - 6010 to 12<br/>
						<v-icon small>mdi-email</v-icon> aissi@alsterph.com<br/>
					</span>
				</v-col>
				
				<v-col cols="3" md="4" lg="6" xl="6" class="hidden-sm-only text-sm-center text-md-center text-lg-center text-xl-center">
					<span class="font-weight-bold text-h6">
						CERTIFICATIONS<br/>
					</span>
					<v-img
						:src="require('@/assets/images/logo/isoLogo.png')"
						width="75"
						height="75"
						class="mx-md-auto mx-lg-auto mx-xl-auto"
					></v-img>
				</v-col>
				
				<v-col cols="12" sm="6" md="4" lg="3" xl="3">
					<span class="font-weight-bold text-h6">
						GET NOTIFICATIONS<br/>
					</span>
					<span class="caption font-weight-light">
						Be the first to know our latest job opening
						<v-text-field
							append-icon="mdi-send"
							dense
							outlined
							placeholder="Email Address"
							v-model="emailNotified"
							:error="error"
							:error-messages="errorMsg"
							@blur="clear"
							@keydown="clear"
							@click="clear"
							@click:append="getNotification"
						></v-text-field>
					</span>
				</v-col>
			</v-row>
			
			<v-divider dark class="my-1"></v-divider>
			
			<v-row no-gutters>
				<v-col cols="12" class="text-center">
					<v-btn text x-small @click="privacyPolicy">Privacy Policy</v-btn> | 
					<v-btn text x-small @click="termsOfUse">Terms of Use</v-btn><br/>
					<span class="caption font-weight-light"> {{ new Date().getFullYear() }} ALSTER. ALL RIGHTS RESERVED.</span>
				</v-col>
				
				<v-dialog
					v-model="dialog"
					width="500"
					scrollable
				>
					<v-card>
						<v-card-title
							class="headline grey lighten-2"
							primary-title
						>
							{{ dialogTitle }}
							<v-spacer></v-spacer>
							
							<v-btn icon
								@click="dialog = false"
							>
								<v-icon small>mdi-close</v-icon>
							</v-btn>
						</v-card-title>

						<v-card-text>
							Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
						Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
						</v-card-text>

						<v-divider></v-divider>

						<v-card-actions>
							<v-spacer></v-spacer>
							<v-btn
								color="primary"
								text
								@click="dialog = false"
							>
								Close
							</v-btn>
						</v-card-actions>
					</v-card>
				</v-dialog>
			</v-row>
		</v-container>
	</v-footer>
	
</template>

<script>

import { PAGE_NOTIFICATION } from '@/store/actions/page';

export default {
	data() {
		return {
			dialog: false,
			dialogTitle: '',
			error: false,
			errorMsg: '',
			emailNotified: '',
		}
	},
	methods: {
		gotoFacebook() {
			window.open('https://www.facebook.com/Alsterph1');
		},
		clear (){
			this.error = false;
			this.errorMsg = '';
		},
		getNotification () {
			if( /.+@.+\..+/.test(this.emailNotified) && this.emailNotified != '' ){
				this.$store.dispatch(PAGE_NOTIFICATION).then(() => {
					this.emailNotified = '';
				});
			}else{
				this.error = true;
				this.errorMsg = 'Enter valid email';
			}
		},
		privacyPolicy (){
			this.dialogTitle = 'Privacy Policy';
			this.dialog = true;
		},
		termsOfUse (){
			this.dialogTitle = 'Terms of Use';
			this.dialog = true;
		},
	}
}

</script>