<template>
	<v-container fluid class="pa-0 ma-0" v-intersect="pageViewIn">
		<v-responsive 
			:aspect-ratio="16/9"
			:height="this.$store.getters.pageHeight"
			:width="this.$store.getters.pageWidth"
		>
			<v-carousel
				v-model="carousel"
				cycle
				height="100%"
				hide-delimiter-background
				hide-delimiters
				:show-arrows=false
			>
				<v-carousel-item
					v-for="(slide, i) in slideContent"
					:key="i"
					reverse-transition="fade-transition"
					transition="fade-transition"
				>
					<v-img class="d-flex primary" :height="pageHeight + 10" position="top right" :src="require(`@/assets/images/alster/${slide.image}`)" alt="Seafarer Image">
						<v-row no-gutters align="center" class="text-h5" v-bind:style="{ height: pageHeight - 100 +'px'}">
							<v-col cols="12" class="text-xs-h6 text-sm-h4 text-md-h3 text-lg-h2 text-xl-h1 ml-2 ml-sm-10 ml-md-10 ml-lg-16 ml-xl-16 font-weight-bold typewriter">
									AT ALSTER YOU CAN<br/>
									{{ caption }}<span></span>&#8205;
							</v-col>
						</v-row>
					</v-img>
				</v-carousel-item>
			</v-carousel>
			
		</v-responsive>
	</v-container>
</template>

<script>

import { mapGetters } from 'vuex';

import { PAGE_VIEW } from '@/store/actions/page';

export default {
	data: () => ({
		page: 'Home',
		caption: '',
		captionIndex: 0,
		captionPosition: 0,
		captionArray: [
			/*'BE A CERTIFIED POGI', 'BECOME RICH', 'CASH ADVANCE ANYTIME'*/
			'SECURE YOUR FUTURE', 'BUILD YOUR CAREER', 'BE A MODERN SEAFARER'
		],
		step: 1,
		carousel: null,
		slideContent: [
			{
				image: "homeSailor.jpg"
			}
		]
	}),
	methods: {
		pageViewIn: function(e){
			if(e[0].isIntersecting){
				let { page } = this;
				this.$store.dispatch(PAGE_VIEW, { page });
			}
		},
		captionWrite() {
			if (this.captionPosition < this.captionArray[this.captionIndex].length) {
				this.caption += this.captionArray[this.captionIndex].charAt(this.captionPosition);
				this.captionPosition++;
				setTimeout(this.captionWrite, 150);
			}else{
				setTimeout(this.captionDelete, 1000);
			}
		},
		captionDelete() {
			if (this.caption.length > 0) {
				this.caption = this.caption.substring(0, this.caption.length - 1, -1);
				setTimeout(this.captionDelete, 50);
			}else{
				this.captionPosition = 0;
				this.captionIndex++;
				this.captionIndex = ( this.captionIndex == 3 )? 0 : this.captionIndex;
				setTimeout(this.captionWrite, 500);
			}
		}
	},
	computed:{
		...mapGetters(['pageHeight', 'pageView'])
	},
	watch: {
		carousel: function(){
			this.captionWrite();
		},
	}
};
</script>

<style scoped>
	>>> .typewriter span {
		overflow: hidden; /* Ensures the content is not revealed until the animation */
		border-right: .15em solid white; /* The typwriter cursor */
		white-space: nowrap; /* Keeps the content on a single line */
		margin: 0 auto; /* Gives that scrolling effect as the typing happens */
		letter-spacing: .15em; /* Adjust as needed */
		animation: 
			typing 3.5s steps(40, end),
			blink-caret .75s step-end infinite;
	}
	
	/* The typing effect */
	@keyframes typing {
		from { width: 0 }
		to { width: 100% }
	}

	/* The typewriter cursor effect */
	@keyframes blink-caret {
		from, to { border-color: transparent }
		50% { border-color: white; }
	}
</style>